<template>
	<view class="background fix-padding">
		<view class="details_list">
			<view class="details_item flex_between">
				<view>
					<view class="details_item_list">
						<view class="title">采购单号</view>
						<view class="value">{{form.order_no}}</view>
					</view>
					<view class="details_item_list">
						<view class="title">供应商</view>
						<view class="value">{{form.apply_name}}</view>
					</view>
					<view class="details_item_list">
						<view class="title">联系人</view>
						<view class="value">{{form.contacts}}</view>
					</view>
					<view class="details_item_list">
						<view class="title">电话</view>
						<view class="value">{{form.phone}}</view>
					</view>
					
					<view class="details_item_list">
						<view class="title">固话</view>
						<view class="value">{{form.mobile}}</view>
					</view>
					<view class="details_item_list">
						<view class="title">日期</view>
						<view class="value">{{form.date}}</view>
					</view>
				</view>
			</view>
		</view>
		<view class="details_list">
			<view v-for="item in form.detail" class="details_item flex_between">
				<view>
					<view class="details_item_list">
						<view class="title">商品名称</view>
						<view class="value">{{item.goods_name}}</view>
					</view>
					<view class="details_item_list">
						<view class="title">规格型号</view>
						<view class="value">{{item.goods_specs}}</view>
					</view>
					<view class="details_item_list">
						<view class="title">单位</view>
						<view class="value">{{item.goods_unit}}</view>
					</view>
					<view class="details_item_list">
						<view class="title">数量</view>
						<view class="value">{{item.count}}</view>
					</view>
					<view class="details_item_list">
						<view class="title">单价</view>
						<view class="value">￥{{item.price}}</view>
					</view>
					<view class="details_item_list">
						<view class="title">金额</view>
						<view class="value">￥{{item.total_price}}</view>
					</view>
					<view class="details_item_list">
						<view class="title">备注</view>
						<view class="value">{{item.remark}}</view>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				id:'',
				form:[],
			}
		},
		onLoad(option) {
			console.log('option',option)
			if(option.data) {
				this.id = JSON.parse(decodeURIComponent(option.data))
				this.listInterface()
			}
			
		},
		methods: {
			// 接口列表
			listInterface() {
				this.http({
				    url: this.api.work_purchase_detail,
				    method: "POST",
				    data: {
				        uid: this.common.get("uid"),
				        order_id:this.id
				    },
				    success: res => {
				        this.form = res
				    }
				})
			},
			// 返回上一页
			previous() {
				let pages = getCurrentPages(); // 当前页面
				let beforePage = pages[pages.length - 2]; // 上一页
				uni.navigateBack({
				    success: function() {
				        beforePage.onLoad(); // 执行上一页的onLoad方法
				    }
				});
			},
		}
	}
</script>

<style lang="scss">
.background {
	// background: #F6F6F6;
}

.details_list {
	// margin-bottom: 20rpx;
	border-bottom: 20rpx solid #F6F6F6;
	.details_item {
		display: flex;
		align-items: flex-start;
		width: 100%;
		padding: 20rpx 30rpx;
		background: $color-f;
		 box-sizing: border-box;
		 border-bottom: 2rpx solid #ECECEC;
		.details_item_list {
			display: flex;
			flex-direction: row;
			font-size: 28rpx;
			margin-bottom: 10rpx;
			.title {
				width: 130rpx;
				font-weight: 400;
				color: $color-6;
				margin-right: 40rpx;
				text-align-last: justify;
			}
			.title_style {
				width: 142rpx;
			}
			.value {
				width: 500rpx;
				font-weight: 500;
				color: $color-3;
			}
		}
	}
}

</style>
